<template>
  <div class="teachersifon padding students">
     <crumb v-if="isOperate ==='taskSituation'" :data="pageTitle"></crumb>
     <crumb v-else style="padding: 0 5px;"></crumb>
    <div class="teachersifon-inp">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-row >
          <el-col :span=" fullWidth == 1920 ? 22 : 24">
                <span class="ssssss">学年：</span>
                <el-select v-model="value" placeholder="请选择学年" class="pppp" :style="{'width': fullWidth == 1920 ? '15%' : '18%'}"  @change="luiii">
                  <el-option v-for="item in options" :key="item" :label="`${item}学年`" :value="item"></el-option>
                </el-select>
                <span class="sssss">学期：</span>
                <el-select v-model="value1" placeholder="请先选择学年" class="pppp" :style="{'width': fullWidth == 1920 ? '15%' : '18%'}">
                  <el-option v-for="item in options1" :key="item" :label="item" :value="item"></el-option>
                </el-select>
                <span class="sssss">代课班级：</span>
                <el-select v-model="value2" placeholder="全部" class="pppp" :style="{'width': fullWidth == 1920 ? '15%' : '18%'}">
                  <el-option v-for="item in options2" :key="item.id" :label="item" :value="item"></el-option>
                </el-select>
                <span class="sssss">导入情况：</span>
                <el-select v-model="finishType" placeholder="全部" class="pppp" :style="{'width': fullWidth == 1920 ? '15%' : '18%'}">
                  <el-option v-for="item in  importOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
                </el-select>
                <span class="sssss" v-if="fullWidth == 1920">代课班级：</span>
                <el-input v-model="formInline.user" class="pppp" :style="{'width': fullWidth == 1920 ? '15%' : '18%'}" placeholder="请输入搜索关键词" v-if="fullWidth == 1920"></el-input>
          </el-col>
          <el-col :span="2" v-if="fullWidth == 1920">
                <div class="search--btn" @click="onSubmit" style="margin: 5px 0 0 20px">搜索</div>
          </el-col>
        </el-row>
        <el-row v-if="fullWidth !== 1920">
          <el-col :span="6">
                <span class="sssss" style="margin-left: 0px;">代课班级：</span>
                <el-input v-model="formInline.user" class="pppp"   style="width:70%" placeholder="请输入搜索关键词"></el-input>
          </el-col>
          <el-col :span="2">
             
                <div class="search--btn" @click="onSubmit" style="margin: 5px 0 0 20px">搜索</div>
          </el-col>
        </el-row>
      </el-form>
    </div>

    <div class="teachersifon-tab">
      <template>
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column label="课程编码" >
            <template slot-scope="scope">
              <span>{{ scope.row.courseCode }}</span>
            </template>
          </el-table-column>
          <el-table-column label="课程名称" >
            <template slot-scope="scope">
              <div
                @click="jump(scope.row.courseId,scope.row.schedulingId)"
                style="cursor: pointer;"
              >
                <span>{{ scope.row.courseName }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="课程类型"  >
            <template slot-scope="scope">
              <span>{{ scope.row.courseClassification }}</span>
            </template>
          </el-table-column>
           <el-table-column label="是否导入">
            <template slot-scope="scope">
              <span>{{ scope.row.exportFinish }}</span>
            </template>
          </el-table-column>
          <el-table-column label="年级">
            <template slot-scope="scope">
              <span>{{ scope.row.grade }}</span>
            </template>
          </el-table-column>
          <el-table-column label="带课班级">
            <template slot-scope="scope">
              <span>{{ scope.row.className }}</span>
            </template>
          </el-table-column>
          <el-table-column label="考核方式"  >
            <template slot-scope="scope">
              <span>{{ scope.row.courseExamine }}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="330"   v-if="isOperate !=='taskSituation'">
            <template slot-scope="scope">
              <!-- <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">
                <i class="el-icon-edit"></i>&nbsp;&nbsp;发起课程评价
              </el-button>-->
              <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                <svgIcon icon-class="icondaochu-excel"></svgIcon>&nbsp;&nbsp;导入成绩
              </el-button>
              <el-button
                v-if="roleName!=='教师' && roleName!=='课程负责人'"
                type="text"
                size="small"
                @click="change(scope.$index, scope.row)"
              >
                <svgIcon icon-class="iconnav-xitongguanli"></svgIcon>&nbsp;&nbsp;维护课程目标成绩关系
              </el-button>
              <el-button
               v-if="roleName==='教师' && scope.row.formId !=='null' && scope.row.formStatus === 1"
                type="text"
                size="small"
                @click="handleStart(scope.row)"
                >
                <svgIcon icon-class="icondaochu-excel"></svgIcon>&nbsp;发起调查
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div>
          <!-- 弹框组件 -->
          <form :model="form" enctype="Content-Type: multipart/form-data">
            <el-dialog
              title="导入成绩"
              :visible.sync="dialogFormVisible"
              :show-close="true"
              :close-on-click-modal="false"
            >
              <div style="height: 350px;" class="steps-dv1">
                <el-steps :active="index" direction="vertical">
                  <el-step title="上传文件" icon="el-icon-edit">
                    <template slot="description">
                      <div>
                        导入学生成绩之前，请先“
                        <span @click="xiazai" class="span-dv">下载模板</span>”，按照模板格式录入数据。
                      </div>
                    </template>
                  </el-step>
                  <el-step title="检查数据" icon="el-icon-upload" description="等待文件上传完毕后自动检查表格数据。">
                    <template slot="description">
                      <div>等待文件上传完毕后自动检查表格数据。</div>
                    </template>
                  </el-step>
                  <el-step title="完成" icon="el-icon-picture"></el-step>
                </el-steps>
                <!-- <el-button @click="dialogFormVisible = false" type="primary" class="steps-btn">上传文件</el-button> -->
                <el-upload
                  class="upload-demo steps-btn"
                  ref="upload"
                  :action="uploadUrl()"
                  accept=".xls"
                  :on-remove="handleRemove"
                  :file-list="fileList"
                  :auto-upload="true"
                  :on-success="handleSuccess"
                  :on-error="handleError"
                  :before-remove="beforeRemove"
                  :limit="1"
                  :on-exceed="handleExceed"
                >
                  <el-button slot="trigger" size="small" type="primary">上传</el-button>
                  <div slot="tip" class="el-upload__tip">支持文件格式：.xls 类型的文件，且不超过20M</div>
                </el-upload>
              </div>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitUpload ">确 定</el-button>
              </div>
            </el-dialog>
          </form>
        </div>
      </template>
    </div>
    <div>
      <template>
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="pageSize"
             layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import { setTimeout } from "timers";

export default {
  data() {
    return {
      fullWidth:document.documentElement.clientWidth, //计算屏幕的宽度
      pageTitle:['成绩管理', '成绩管理'],
      dialogFormVisible: false,
      isOperate:this.$route.query.type, //教学任务管理-任务完成情况跳转过来携带 type:taskSituation无操作权限
      roleName: localStorage.getItem("roleName"),
      formLabelWidth: "120px",
      currentPage4: 1,
      pageSize: 15,
      total: 0,
      file: "",
      index: 1,
      fileList: [],
      formInline: {
        user: ""
      },
      finishType:'',
      importOptions:[
        {
            name:'全部',
            id:''
        },
        {
            name:'已导入',
            id:'已导入'
        },
        {
            name:'未导入',
            id:'未导入'
        }
      ],
      tableData1: [],
      tableData: [],
      options: [],
      value: "",
      options1: [],
      value1: "",
      options2: [],
      value2: "",
      teacherssId: {},
      form: {
        file: ""
      },
      arr: "",
      api:{
         startInvestigation: "/form/rest/FormMainBaseBusiness/startCourseInvestigation/v1"
      }
    };
  },
  methods: {
    submitUpload() {
      if (this.index === 3) {
        this.dialogFormVisible = false;
        this.index = 1;
      }
    },
    uploadUrl() {
      let token = localStorage.getItem("x_token");
      if (this.teacherssId.classIds) {
        return `${
          this.API_URL
        }/teacher/course/upload?x-token=${token}&courseId=${
          this.teacherssId.courseId
        }&classIds=${this.teacherssId.classIds.join(",")}&grade=${this.teacherssId.grade}`;
      }
    },
    handleRemove(file, fileList) {
      this.form.file = "";
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
      if (files.size > 20480) {
        this.$message.warning(`sorry 上传文件大于20M,请重新上`);
      }
    },
    // 文件上传成功时的钩子
    handleSuccess(res, file, fileList) {
      if (res.code === 200) {
        this.$notify.success({
          title: "success",
          message: res.message
        });
        this.index = 3;
      } else {
        this.$message({
          type: "info",
          message: res.message
        });
        this.index = 2;
        //this.handleError()
      }
    },
    // 文件上传失败时的钩子
    handleError(err, file, fileList) {
      // this.$notify.error({
      //   title: '错误',
      //   message: `文件上传失败`
      // });
    },
    beforeRemove(file, fileList) {
      if (this.index === 3) {
        this.index = 1;
      }
      this.$confirm(`确定移除 ${file.name}？`);
    },
    onSubmit() {
      let grade = this.value;
      let term = this.value1;
      let className = this.value2;
      let courseName = this.formInline.user;
      let pageNum = this.currentPage4;
      let pageSize = this.pageSize;
      let versionId = localStorage.getItem("versionId");
      const finishType=this.finishType
      this.$post("/teacher/mycourse/getall", {
        grade,
        term,
        className,
        courseName,
        pageNum,
        pageSize,
        versionId,
        finishType
      }).then(res => {
        this.tableData = res.list;
        this.total = res.rowCount;
      });
      // this.getall()
    },
    luiii(value) {
      if (value == "请选择") {
        this.options1 = [];
        this.options1.push("请选择");
      } else {
        this.arr = "abc";
        this.getall();
      }
    },
    // 搜索链接    /teacher/mycourse/bycondition
    getall() {
      let arr = {
        grade: this.value,
        term: this.value1,
        className: this.value2,
        courseName: this.formInline.user,
        pageNum: this.currentPage4,
        pageSize: this.pageSize,
        versionId: localStorage.getItem("versionId")
      };
      this.$post("/teacher/mycourse/getall", arr).then(res => {
        if (res.success == 1) {
          this.tableData = res.list;
          this.total = res.rowCount;
          let obj2 = { name: "", id: "" };
          let obj1 = { name1: "", id1: "" };
          let arr = [];
          let arr1 = [];
          let arr2 = [];
          res.grades.forEach(item => {
            arr.push(item.grade);
          });
          this.options = arr;
          res.classes.forEach(item => {
            arr1.push(item.className);
          });
          this.options2 = arr1;
          if (this.arr == "abc") {
            res.terms.forEach(item => {
              arr2.push(item.courseTerm);
            });
            this.options1 = arr2;
          }
        }
      });
    },
    handleEdit(index, row) {
      //编辑对话框显示值
    },
    seccons4() {
      // 确定按钮的点击提交编辑内容
      this.dialogFormVisible = false;
    },
    close() {
      this.dialogFormVisible = true;
    },
    change(index, row) {
      let id = row.courseId;
      this.$router.push({ path: "/teachers/weihu", query: { id: id } });
    },
    // 路由传参
    jump(id, schedulingId) {
      this.$router.push({
        path: "/studentside/graduation",
        query: { id: id, schedulingId: schedulingId }
      });
    },
    xiazai() {
      const token = localStorage.getItem("x_token");
      window.location.href = `${
        this.API_URL
      }/teacher/course/downmodel?x-token=${token}&courseId=${
        this.teacherssId.courseId
      }&classIds=${this.teacherssId.classIds.join(",")}&grade=${this.teacherssId.grade}`;
    },
    handleDelete(index, row) {
      this.dialogFormVisible = true;
      this.teacherssId = row;
    },
    handleStart(row) {
      this.$confirm(`确认发起${row.courseName}的达成评价?`, "确认提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        const postData={
              formId:row.formId,
              classIds:row.classIds.join(',') 
        }
        this.$post(this.api.startInvestigation, postData).then(
          res => {
            if (res.code === 200) {
                this.$notify.success({
                  title: "success",
                  duration: 1500,
                  message: "操作成功"
                });
            }
          }
        );
      });
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getall();
    },
    handleCurrentChange(val) {
      this.currentPage4 = val;
      this.getall();
    }
  },
  mounted() {
    this.getall();
  }
};
</script>
<style lang="scss" >
.teachersifon {
  
  .teachersifon-inp {
    width: 100%;
    overflow: hidden;
    background: #fff;
    padding: 13px 20px 15px;
    margin-bottom: 20px;
    box-sizing: border-box;
    border-radius: 10px;
    .pppp {
      width: 15%;
      float: left;
      margin: 0;
      margin-top: 6px;
    }
  }
  .teachersifon-tab {
    background-color: #fff;
    border-radius: 10px 10px 0 0;
    padding-bottom: 0;
    .el-table .cell{
      padding-left: 0;
    }
  }

  .ssssss {
    float: left;
    line-height: 50px;
    font-weight: bold;
    font-size: 14px;
    color: #696969;
  }
  .sssss {
    margin-left: 16px;
    line-height: 50px;
    float: left;
    font-weight: bold;
    font-size: 14px;
    color: #696969;
  }
  .el-form--inline .el-form-item {
    margin-left: 20px;
  }
  .span-dv {
    cursor: pointer;
    color: #000;
    padding: 0 8px;
  }
  .teachersifon-btn {
    margin-top: 18px;
  }
  .teachersifon-btn1 {
    margin-top: 18px;
  }
  .block {
    float: left;
    width: 100%;
    text-align: right;
    background-color: #fff;
    padding: 20px;
    border-radius: 0 0 10px 10px;
    box-sizing: border-box;
    margin-bottom: 100px;
  }
  .steps-dv1 {
    position: relative;
    .steps-btn {
      position: absolute;
      top: 66px;
      left: 54px;
    }
  }
  .el-dialog {
    width: 33%;
  }
  .el-button {
    padding-top: 6px;
    padding-bottom: 6px;
  }
}
</style>



